// import $ from 'jquery'
import toastr from 'toastr';
import HoSoTrongNhom from '.././TiepNhan/HoSoTrongNhom';
toastr.options = {
  'closeButton': true,
  'timeOut': '5000'
};
export default {
  props: {
    id: {
      type: String,
      default: function _default() {
        return 'nm';
      }
    },
    detailDossier: {
      type: Object,
      default: function _default() {}
    },
    createFiles: {
      type: Array,
      default: function _default() {
        return [];
      }
    },
    esignType: {
      type: String,
      default: function _default() {
        return '';
      }
    }
  },
  components: {
    'ho-so-nhom': HoSoTrongNhom
  },
  data: function data() {
    return {
      dialogConfirm: false,
      labelConfirm: '',
      dossierTemplateItems: [],
      dossierMarksItems: [],
      dossierFilesItems: [],
      thongTinHoSo: {},
      valid: true,
      stateView: false,
      dialogAddOtherTemp: false,
      loadingAddOther: false,
      otherDossierTemplate: '',
      sheet: false,
      partView: '',
      dossierFileIdView: '',
      stateEdit: false,
      progressUploadPart: '',
      dialogPDF: false,
      dialogPDFLoading: true,
      stateAddFileOther: false,
      dossierTemplatesItemSelect: {},
      fileViews: [],
      sampleCount: 0,
      fileTemplateItems: [],
      currentFormView: '',
      pstFixed: 0,
      pstEl: 0,
      endEl: 0,
      dialogSelectDosier: false,
      dossierIntoGroup: [],
      filesAdd: [],
      documentType: 'Tài liệu đính kèm',
      receiveMessage: '',
      active: true,
      loadingApacal: false
    };
  },
  computed: {
    loading: function loading() {
      return this.$store.getters.loading;
    },
    originality: function originality() {
      var vm = this;
      return vm.getOriginality();
    },
    initDataResource: function initDataResource() {
      return this.$store.getters.loadingInitData;
    },
    dossierIntoGroupSelect: function dossierIntoGroupSelect() {
      return this.$store.getters.getDossierIntoGroup;
    },
    createFileSignedSync: function createFileSignedSync() {
      return this.$store.getters.getCreateFileSigned;
    },
    userLoginInfomation: function userLoginInfomation() {
      return this.$store.getters.getUserLogin;
    }
  },
  created: function created() {
    var vm = this;

    vm.receiveMessage = function (event) {
      vm.saveAlpacaFormCallBack(event);
    };

    vm.page = 1;
    vm.$nextTick(function () {
      // console.log('vm.detailDossier------------', vm.detailDossier)
      if (vm.detailDossier['dossierId']) {
        var arrTemp = [];
        arrTemp.push(vm.$store.dispatch('loadDossierFiles', vm.detailDossier.dossierId));
        arrTemp.push(vm.$store.dispatch('loadDossierTemplates', vm.detailDossier));
        Promise.all(arrTemp).then(function (values) {
          var dossierTemplates = values[1]['dossierParts'];
          var dossierFiles = values[0];
          vm.dossierFilesItems = dossierFiles; //

          vm.dossierFilesItems.forEach(function (template, index) {
            if (vm.detailDossier['dossierId'] === vm.createFileSignedSync['dossierId']) {
              template['isSigned'] = vm.createFileSignedSync['createFiles'][index]['isSigned'];
              template['fileEntryId'] = vm.createFileSignedSync['createFiles'][index]['fileEntryId'];
              template['pdfSigned'] = vm.createFileSignedSync['createFiles'][index]['pdfSigned'];
            } else {
              template['isSigned'] = false;
              template['fileEntryId'] = '';
              template['pdfSigned'] = '';
            }
          }); // 

          vm.createFiles = vm.mergeDossierTemplateVsDossierFiles(vm.createFiles, dossierFiles);

          if (dossierTemplates.length !== 0) {
            vm.createFiles.forEach(function (template) {
              var itemFind = dossierTemplates.find(function (part) {
                return template.partNo === part.partNo;
              });

              if (itemFind) {
                template['required'] = itemFind['required'];
                template['partTip'] = itemFind['partTip'];
              }
            });
          }
        }); // danh sách hồ sơ cùng group

        if (vm.detailDossier['groupDossierId']) {
          vm.getDossierIntoGroup(vm.detailDossier['groupDossierId']);
        }
      }
    });
  },
  watch: {
    '$route': function $route(newRoute, oldRoute) {}
  },
  mounted: function mounted() {
    var vm = this;
    vm.$nextTick(function () {
      if (vm.createFiles.length > 0) {
        setTimeout(function () {
          vm.genAllAlpacaForm(vm.dossierFilesItems, vm.createFiles);
        }, 300);
      }
    });
  },
  methods: {
    genAllAlpacaForm: function genAllAlpacaForm(dossierFiles, createFiles) {
      var vm = this;

      if (dossierFiles.length > 0 && dossierFiles.filter(function (file) {
        return file.eForm && file.size;
      }).length > 0) {
        var dossierFilesEform = dossierFiles.filter(function (file) {
          return file.eForm;
        });
        var dossierTemplatesHasForm = createFiles.filter(function (template) {
          return template.eForm;
        });

        if (dossierFilesEform.length > 0) {// dossierFilesEform.forEach(itemFiles => {
          //   if (itemFiles.eForm) {
          //     vm.$store.dispatch('loadAlpcaForm', itemFiles)
          //   }
          // })
        } else {
          createFiles.forEach(function (val) {
            if (val.eForm) {
              val['templateNo'] = vm.detailDossier.dossierTemplateNo;
              vm.showAlpacaJSFORM(val);
            }
          });
        }

        if (dossierTemplatesHasForm.length !== dossierFilesEform.length) {
          dossierTemplatesHasForm.forEach(function (template) {
            var indexFromFile = dossierFilesEform.findIndex(function (item) {
              return template.partNo === item.dossierPartNo;
            });

            if (indexFromFile === -1) {
              template['templateNo'] = vm.detailDossier.dossierTemplateNo;
              vm.showAlpacaJSFORM(template);
            }
          });
        }
      } else {
        createFiles.forEach(function (val) {
          if (val.eForm) {
            val['templateNo'] = vm.detailDossier.dossierTemplateNo;
            vm.showAlpacaJSFORM(val);
          }
        });
      }
    },
    mergeDossierTemplateVsDossierFiles: function mergeDossierTemplateVsDossierFiles(createFiles, dossierFiles) {
      var vm = this;

      if (dossierFiles.length !== 0) {
        createFiles.forEach(function (template, index) {
          template['editForm'] = true;
          template['embed'] = false;
          var itemFind = dossierFiles.find(function (file) {
            return template.partNo === file.dossierPartNo && file.fileSize !== 0; // return template.partNo === file.dossierPartNo && file.eForm
          });

          if (itemFind) {
            template['daKhai'] = true;
          } else if (!itemFind && template.eForm) {
            template['daKhai'] = false;
          }
        });
      } else {
        createFiles.forEach(function (template) {
          if (template.eForm) {
            template['daKhai'] = false;
            template['editForm'] = true;
            template['embed'] = false; // template['isSigned'] = false
            // template['fileEntryId'] = ''
            // template['pdfSigned'] = ''
          }
        });
      }

      return createFiles;
    },
    showAlpacaJSFORM: function showAlpacaJSFORM(item) {
      var vm = this;
      item['dossierId'] = vm.detailDossier.dossierId;
      item['editForm'] = true;
      item.templateNo = vm.detailDossier.dossierTemplateNo;
      /* eslint-disable */

      var eformScript = '';

      if (item.formScript) {
        try {
          eformScript = eval('(' + item.formScript + ')');
        } catch (error) {}
        /* eslint-disable */


        if (eformScript && eformScript.hasOwnProperty('eformEmbed') && eformScript.eformEmbed) {
          item.embed = true;
          vm.active = false;
          vm.active = true;
          var userId = window.themeDisplay.getUserId();
          var userEmail = vm.originality === 1 ? vm.userLoginInfomation.applicantContactEmail : vm.userLoginInfomation.employeeEmail;
          var referenceUid = vm.detailDossier.referenceUid;
          var dossierStatus = vm.detailDossier.dossierStatus;
          var dossierSubStatus = vm.detailDossier.dossierSubStatus;
          var templateNo = item.partNo;
          var deliverableType = item.deliverableType ? item.deliverableType : '';
          var token = localStorage.getItem('jwt_token');
          var urlEmbed = eformScript.eformEmbed + '/' + item.templateFileNo + '/referenceUid/' + referenceUid + '/' + token + '/' + encodeURIComponent(document.location.origin) + '?userId=' + userId + '&userEmail=' + userEmail + '&code=' + referenceUid + '&dossierStatus=' + dossierStatus + '&dossierSubStatus=' + dossierSubStatus + '&tp=' + templateNo;
          setTimeout(function () {
            document.getElementById('formAlpaca' + item.partNo + vm.id + 'embed').src = '';
            setTimeout(function () {
              document.getElementById('formAlpaca' + item.partNo + vm.id + 'embed').src = urlEmbed;
            }, 50);
          }, 300);
        } else {
          item.embed = false;
          vm.active = false;
          vm.active = true;
          vm.$store.dispatch('loadFormData', item).then(function (resData) {
            window.$('#formAlpaca' + item.partNo + vm.id).empty();
            var formScript, formData;
            /* eslint-disable */

            if (item.formScript) {
              formScript = eval('(' + item.formScript + ')');
            } else {
              formScript = {};
            }

            if (resData) {
              formData = eval('(' + resData + ')');
            } else {
              formData = {};
            } // giấy phép


            formData.dossierId_hidden = vm.detailDossier.dossierId;
            formData.dossierStatus_hidden = vm.detailDossier.dossierStatus;
            formData.dossierSubStatus_hidden = vm.detailDossier.dossierSubStatus;
            formData.fileTemplateNo_hidden = item.templateFileNo;
            formData.deliverableType_hidden = item.deliverableType;
            formData.userEmailAddress_hidden = vm.originality === 1 ? vm.userLoginInfomation.applicantContactEmail : vm.userLoginInfomation.employeeEmail;
            formData.referenceUid = vm.detailDossier.referenceUid;

            /* eslint-disable */
            formScript.data = formData;
            window.$('div[id="formAlpaca' + item.partNo + vm.id + '"]').alpaca(formScript);
          });
        } // 

      } else {
        vm.$store.dispatch('loadFormScriptKQ', item).then(function (resScript) {
          try {
            eformScript = eval('(' + resScript + ')');
          } catch (error) {}
          /* eslint-disable */


          if (eformScript && eformScript.hasOwnProperty('eformEmbed') && eformScript.eformEmbed) {
            item.embed = true;
            vm.active = false;
            vm.active = true;

            var _userId = window.themeDisplay.getUserId();

            var _userEmail = vm.originality === 1 ? vm.userLoginInfomation.applicantContactEmail : vm.userLoginInfomation.employeeEmail;

            var _referenceUid = vm.detailDossier.referenceUid;
            var _dossierStatus = vm.detailDossier.dossierStatus;
            var _dossierSubStatus = vm.detailDossier.dossierSubStatus;
            var _templateNo = item.partNo;

            var _deliverableType = item.deliverableType ? item.deliverableType : '';

            var _token = localStorage.getItem('jwt_token');

            var _urlEmbed = eformScript.eformEmbed + '/' + item.templateFileNo + '/referenceUid/' + _referenceUid + '/' + _token + '/' + encodeURIComponent(document.location.origin) + '?userId=' + _userId + '&userEmail=' + _userEmail + '&code=' + _referenceUid + '&dossierStatus=' + _dossierStatus + '&dossierSubStatus=' + _dossierSubStatus + '&tp=' + _templateNo;

            setTimeout(function () {
              document.getElementById('formAlpaca' + item.partNo + vm.id + 'embed').src = _urlEmbed;
            }, 300);
          } else {
            item.embed = false;
            vm.active = false;
            vm.active = true;
            vm.$store.dispatch('loadFormData', item).then(function (resData) {
              window.$('#formAlpaca' + item.partNo + vm.id).empty();
              var formScript, formData;
              /* eslint-disable */

              if (item.formScript) {
                formScript = eval('(' + item.formScript + ')');
              } else {
                formScript = {};
              }

              if (resData) {
                formData = eval('(' + resData + ')');
              } else {
                formData = {};
              } // giấy phép


              formData.dossierId_hidden = vm.detailDossier.dossierId;
              formData.dossierStatus_hidden = vm.detailDossier.dossierStatus;
              formData.dossierSubStatus_hidden = vm.detailDossier.dossierSubStatus;
              formData.fileTemplateNo_hidden = item.templateFileNo;
              formData.deliverableType_hidden = item.deliverableType;
              formData.userEmailAddress_hidden = vm.originality === 1 ? vm.userLoginInfomation.applicantContactEmail : vm.userLoginInfomation.employeeEmail;
              formData.referenceUid = vm.detailDossier.referenceUid;

              /* eslint-disable */
              formScript.data = formData;
              window.$('div[id="formAlpaca' + item.partNo + vm.id + '"]').alpaca(formScript);
            });
          } // 

        });
      }
    },
    saveAlpacaForm: function saveAlpacaForm(item, index) {
      var vm = this;
      var fileFind = vm.dossierFilesItems.find(function (itemFile) {
        // return itemFile.dossierPartNo === item.partNo && itemFile.eForm && itemFile.fileSize !== 0
        return itemFile.dossierPartNo === item.partNo && itemFile.eForm;
      });

      if (fileFind) {
        fileFind['dossierId'] = vm.detailDossier.dossierId;
        fileFind['id'] = vm.id;
        vm.$store.dispatch('putAlpacaForm', fileFind).then(function (resData) {
          toastr.clear();
          toastr.success('Yêu cầu của bạn thực hiện thành công');
          vm.createFiles[index].daKhai = true; // Đính kèm giấy tờ hồ sơ cùng nhóm
          // if (vm.dossierIntoGroup.length > 0) {
          //   vm.labelConfirm = 'Đính kèm giấy tờ này cho hồ sơ khác?'
          //   vm.dialogConfirm = true
          //   vm.filesAdd = [resData]
          // }

          vm.$store.dispatch('loadDossierFiles', vm.detailDossier.dossierId).then(function (resFiles) {
            vm.dossierFilesItems = resFiles; // 

            vm.createFileSigned('update'); // 
          }).catch(function (reject) {}); // 
          // vm.createFiles[index]['isSigned'] = false
          // vm.createFiles[index]['fileEntryId'] = ''
          // vm.createFiles[index]['pdfSigned'] = ''
          // let createFileSigned = {
          //   dossierId: vm.detailDossier['dossierId'],
          //   createFiles: vm.createFiles
          // }
          // vm.$store.commit('setCreateFileSigned', createFileSigned)
        }).catch(function (reject) {
          toastr.clear();
          toastr.error('Yêu cầu của bạn thực hiện thất bại.');
        });
      } else {
        item['dossierId'] = vm.detailDossier.dossierId;
        item['id'] = vm.id;
        vm.$store.dispatch('postEform', item).then(function (resPostEform) {
          // Đính kèm giấy tờ hồ sơ cùng nhóm
          // if (vm.dossierIntoGroup.length > 0) {
          //   vm.labelConfirm = 'Đính kèm giấy tờ này cho hồ sơ khác?'
          //   vm.dialogConfirm = true
          //   vm.filesAdd = [resData]
          // }
          setTimeout(function () {
            toastr.success('Yêu cầu của bạn thực hiện thành công');
            vm.createFiles[index].daKhai = true;
          }, 3000);
          vm.$store.dispatch('loadDossierFiles', vm.detailDossier.dossierId).then(function (resFiles) {
            vm.dossierFilesItems = resFiles; // 

            vm.createFileSigned('update'); // 
          }).catch(function (reject) {
            toastr.clear();
            toastr.error('Yêu cầu của bạn thực hiện thất bại.');
          }); // 
          // vm.createFiles[index]['isSigned'] = false
          // vm.createFiles[index]['fileEntryId'] = ''
          // vm.createFiles[index]['pdfSigned'] = ''
          // let createFileSigned = {
          //   dossierId: vm.detailDossier['dossierId'],
          //   createFiles: vm.createFiles
          // }
          // vm.$store.commit('setCreateFileSigned', createFileSigned)
        }).catch(function (reject) {
          toastr.clear();
          toastr.error('Yêu cầu của bạn thực hiện thất bại.');
        });
      }
    },
    saveAlpacaFormCallBack: function saveAlpacaFormCallBack(data) {
      var vm = this;
      var dataOutPut = data.data ? data.data : ''; // console.log('data_output eform', dataOutPut)

      if (dataOutPut.hasOwnProperty('tp') && dataOutPut.tp) {
        var fileFind = vm.dossierFilesItems.find(function (itemFile) {
          return itemFile.dossierPartNo === dataOutPut.tp && itemFile.eForm && itemFile.fileSize !== 0;
        });

        if (fileFind) {
          var index = vm.createFiles.findIndex(function (item) {
            return item.partNo === dataOutPut.tp;
          });
          fileFind['dossierId'] = vm.detailDossier.dossierId;
          vm.$store.dispatch('putAlpacaFormCallBack', fileFind).then(function (resData) {
            setTimeout(function () {
              toastr.clear();
              toastr.success('Thực hiện thành công'); // gen lại sau cập nhật

              vm.createFiles[index].daKhai = true;
              vm.showAlpacaJSFORM(vm.createFiles[index]);
            }, 3000);
            vm.$store.dispatch('loadDossierFiles', vm.detailDossier.dossierId).then(function (resFiles) {
              vm.dossierFilesItems = resFiles;
            }).catch(function (reject) {});
          }).catch(function (reject) {
            toastr.clear();
            toastr.error('Yêu cầu của bạn thực hiện thất bại.');
          });
        } else {
          dataOutPut['dossierId'] = vm.detailDossier.dossierId;
          vm.$store.dispatch('postEformCallBack', dataOutPut).then(function (resPostEform) {
            var index = vm.createFiles.findIndex(function (item) {
              return item.partNo === dataOutPut.tp;
            });
            setTimeout(function () {
              toastr.clear();
              toastr.success('Thực hiện thành công');
              vm.createFiles[index].daKhai = true; // gen lại sau cập nhật

              vm.createFiles[index].daKhai = true;
              vm.showAlpacaJSFORM(vm.createFiles[index]);
            }, 3000);
            vm.$store.dispatch('loadDossierFiles', vm.detailDossier.dossierId).then(function (resFiles) {
              vm.dossierFilesItems = resFiles;
            }).catch(function (reject) {
              toastr.clear();
              toastr.error('Yêu cầu của bạn thực hiện thất bại.');
            });
          }).catch(function (reject) {
            toastr.clear();
            toastr.error('Yêu cầu của bạn thực hiện thất bại.');
          });
        }
      }
    },
    pickFile: function pickFile(item) {
      var vm = this;
      vm.stateAddFileOther = false;
      document.getElementById('file' + item.partNo).value = '';
      document.getElementById('file' + item.partNo).click();
    },
    onUploadSingleFile: function onUploadSingleFile(e, data) {
      var vm = this;
      vm.dossierTemplatesItemSelect = data;
      vm.progressUploadPart = data.partNo;
      data['dossierId'] = vm.detailDossier.dossierId;
      data['dossierTemplateNo'] = vm.detailDossier.dossierTemplateNo;
      vm.$store.dispatch('uploadSingleFile', data).then(function (result) {
        vm.progressUploadPart = '';
        vm.$store.dispatch('loadDossierFiles', vm.detailDossier.dossierId).then(function (result) {
          vm.dossierFilesItems = result;
          vm.createFiles = vm.mergeDossierTemplateVsDossierFiles(vm.createFiles, vm.dossierFilesItems);
          // 
          vm.createFileSigned(); // 
        }); // add hồ sơ cùng nhóm
        // Đính kèm giấy tờ hồ sơ cùng nhóm
        // if (vm.dossierIntoGroup.length > 0) {
        //   // vm.dialogSelectDosier = true
        //   vm.labelConfirm = 'Đính kèm giấy tờ này cho hồ sơ khác?'
        //   vm.dialogConfirm = true
        //   vm.filesAdd = result
        // }
      }).catch(function (xhr) {
        vm.progressUploadPart = '';
      });
    },
    loadAlpcaForm: function loadAlpcaForm(data) {
      var vm = this;
      var fileFind = vm.dossierFilesItems.find(function (itemFile) {
        return itemFile.dossierPartNo === data.partNo && itemFile.eForm && itemFile.fileSize !== 0;
      });

      if (fileFind) {
        fileFind['id'] = vm.id;
        vm.$store.dispatch('loadAlpcaForm', fileFind);
      } else {
        vm.createFiles.forEach(function (val) {
          if (val.eForm && data.partNo === val.partNo) {
            val['templateNo'] = vm.detailDossier.dossierTemplateNo;
            vm.showAlpacaJSFORM(val);
          }
        });
      }
    },
    loadAlpcaFormClick: function loadAlpcaFormClick(data) {
      var vm = this;
      window.removeEventListener('message', vm.receiveMessage);
      window.addEventListener('message', vm.receiveMessage); //

      if (vm.currentFormView === 'formAlpaca' + data.partNo + vm.id) {
        vm.currentFormView = '';
      } else {
        vm.currentFormView = 'formAlpaca' + data.partNo + vm.id;
      }

      vm.pstEl = vm.endEl = 0;
      setTimeout(function () {
        if ($('#formAlpaca' + data.partNo + vm.id).height() > 200) {
          vm.pstEl = $('#wrapForm' + data.partNo + vm.id).offset().top;
          vm.endEl = $('#formAlpaca' + data.partNo + vm.id).height();
          $(window).scroll(function () {
            vm.pstFixed = $(window).scrollTop();
          });
        }
      }, 300); //

      var fileFind = vm.dossierFilesItems.find(function (itemFile) {
        return itemFile.dossierPartNo === data.partNo && itemFile.eForm;
      });

      if (fileFind) {
        if (fileFind.fileSize) {
          fileFind['id'] = vm.id; // vm.$store.dispatch('loadAlpcaForm', fileFind)
          // preview PDF

          data['editForm'] = false; // 

          var indexFile = '';

          for (var i = 0; i < vm.dossierFilesItems.length; i++) {
            if (vm.dossierFilesItems[i]['dossierPartNo'] === data['partNo'] && vm.dossierFilesItems[i]['eForm']) {
              indexFile = i;
              break;
            }
          } // 


          if (!vm.dossierFilesItems[indexFile]['isSigned']) {
