    viewFileAlpaca: function viewFileAlpaca(file) {
      var vm = this;
      var counter = 0;
      vm.loadingApacal = true;

      var callServer = function callServer() {
        setTimeout(function () {
          vm.$store.dispatch('viewFile', file).then(function (result) {
            if (result === 'pending' && counter <= 10) {
              counter += 1;
              callServer();
            } else {
              if (counter > 10) {
                vm.loadingApacal = false;
              } else {
                vm.loadingApacal = false;
                setTimeout(function () {
                  document.getElementById('displayPDF' + file.dossierPartNo + vm.id).src = result;
                }, 100);
              }
            }
          }).catch(function () {
            vm.loadingApacal = false;
          });
        }, 1000);
      };

      callServer();
    },
