    getDocumentTypeIcon: function getDocumentTypeIcon(type) {
      var vm = this;
      var typeDoc = 'doc,docx';
      var typeExcel = 'xls,xlsx';
      var typeImage = 'png,jpg,jpeg';

      if (type) {
        if (typeDoc.indexOf(type.toLowerCase()) >= 0) {
          return {
            icon: 'fas fa fa-file-word-o',
            color: 'blue',
            size: 14
          };
        } else if (typeExcel.indexOf(type.toLowerCase()) >= 0) {
          return {
            icon: 'fas fa fa-file-excel-o',
            color: 'green',
            size: 14
          };
        } else if (type.toLowerCase() === 'pdf') {
          return {
            icon: 'fa fa-file-pdf-o',
            color: 'red',
            size: 14
          };
        } else if (typeImage.indexOf(type.toLowerCase()) >= 0) {
          return {
            icon: 'fas fa fa-file-image-o',
            color: 'primary',
            size: 14
          };
        } else {
          return {
            icon: 'fas fa fa-paperclip',
            color: '',
            size: 14
          };
        }
      } else {
        return {
          icon: 'attach_file',
          color: 'primary',
          size: 14
        };
      }
    }
