export default {
  props: {
    detailDossier: {
      type: Object,
      default: function _default() {}
    },
    mauCongVan: {
      type: String,
      default: function _default() {
        return '';
      }
    }
  },
  watch: {
    detailDossier: function detailDossier(val) {
      // console.log('watch', val)
      var vm = this;
      vm.thongTinChiTietHoSo = val;

      try {
        vm.metaData = JSON.parse(val.metaData);
      } catch (error) {}

      if (vm.thongTinChiTietHoSo['groupDossierId']) {
        vm.$store.dispatch('getDetailDossier', vm.thongTinChiTietHoSo['groupDossierId']).then(function (resultDossier) {
          vm.groupDossierNo = resultDossier['dossierNo'];
          vm.groupDossierName = resultDossier['dossierName'];
        });
      }
    }
  },
  components: {},
  data: function data() {
    return {
      thongTinChiTietHoSo: {},
      metaData: '',
      groupDossierNo: '',
      groupDossierName: '',
      showContactDetail: false,
      xacthuc_BNG: false
    };
  },
  computed: {
    loading: function loading() {
      return this.$store.getters.loading;
    },
    originality: function originality() {
      var vm = this;
      return vm.getOriginality();
    }
  },
  created: function created() {
    var vm = this;
    vm.thongTinChiTietHoSo = vm.detailDossier;

    if (vm.thongTinChiTietHoSo['groupDossierId']) {
      vm.$store.dispatch('getDetailDossier', vm.thongTinChiTietHoSo['groupDossierId']).then(function (resultDossier) {
        vm.groupDossierNo = resultDossier['dossierNo'];
        vm.groupDossierName = resultDossier['dossierName'];
      });
    }

    try {
      if (xacthuc_BNG) {
        vm.xacthuc_BNG = true;
      }
    } catch (error) {}
  },
  mounted: function mounted() {
    var vm = this;
    vm.thongTinChiTietHoSo = vm.detailDossier;
  },
  methods: {
    initData: function initData(data) {
      var vm = this;
      vm.$store.dispatch('getDetailDossier', data).then(function (resultDossier) {
        vm.thongTinChiTietHoSo = resultDossier;
      });
    },
    goBack: function goBack() {
      window.history.back();
    },
    durationText: function durationText(durationUnit, durationCount) {
      var durationText;

      if (durationUnit === 1 && durationCount > 8) {
        var day = Math.floor(durationCount / 8) + ' ngày';
        var hours;

        if (durationCount % 8 !== 0) {
          hours = (durationCount / 8 - Math.floor(durationCount / 8)) * 8 + ' giờ';
        } else {
          hours = '';
        }

        durationText = "".concat(day, " ").concat(hours);
      } else if (durationUnit === 0) {
        durationText = durationCount + ' ngày';
      } else if (durationUnit === 1 && durationCount <= 8) {
        durationText = durationCount + ' giờ';
      }

      return durationText;
    },
    detailGroup: function detailGroup() {
      var vm = this;

      if (vm.thongTinChiTietHoSo['permission'].indexOf('write') >= 0) {
        vm.$router.push('/danh-sach-ho-so/0/nhom-ho-so/' + vm.thongTinChiTietHoSo['groupDossierId']);
      }
    },
    currency: function currency(value) {
      if (value) {
        var moneyCur = (value / 1).toFixed(0).replace('.', ',');
        return moneyCur.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.');
      }

      return '';
    },
    checkSoBan: function checkSoBan(thongTinChiTietHoSo) {
      try {
        var metaData = JSON.parse(thongTinChiTietHoSo.metaData);

        if ('tongSoBan' in metaData) {
          return true;
        } else {
          return false;
        }
      } catch (err) {
        return false;
      }
    },
    checkTongSoTien: function checkTongSoTien(thongTinChiTietHoSo) {
      try {
        var metaData = JSON.parse(thongTinChiTietHoSo.metaData);

        if ('tongSoTien' in metaData) {
          return true;
        } else {
          return false;
        }
      } catch (err) {
        return false;
      }
    },
    formarMoney: function formarMoney(str) {
      var formatter = new Intl.NumberFormat('vi-VN', {
        style: 'currency',
        currency: 'VND'
      });
      return formatter.format(parseFloat(str));
    }
  },
  filters: {
    dateTimeView: function dateTimeView(arg) {
      if (arg) {
        var value = new Date(arg);
        return "".concat(value.getDate().toString().padStart(2, '0'), "/").concat((value.getMonth() + 1).toString().padStart(2, '0'), "/").concat(value.getFullYear(), " | ").concat(value.getHours().toString().padStart(2, '0'), ":").concat(value.getMinutes().toString().padStart(2, '0'));
      } else {
        return '';
      }
    }
  }
};